/* HELPERS
------------------------------------------------------------------------------*/
/* THEME COMPONENTS
------------------------------------------------------------------------------*/
.accordion-item.border-b-4 {
  border-bottom-width: 4px !important;
}

.wicket-breadcrumb .icon {
  display: block;
  margin: 0 10px;
}
.wicket-breadcrumb span.bold {
  font-weight: bold;
}
.wicket-breadcrumb.reversed {
  color: #fff;
}
.wicket-breadcrumb.reversed .icon {
  color: #fff;
}

/* Base Button */
.button {
  background-color: transparent;
  border: 2px solid var(--wp--preset--color--primary);
  padding: 14px 16px;
  font-size: var(--wp--preset--font-size--body-md, 16px);
  cursor: pointer;
  text-decoration: none;
  border-radius: var(--wp--custom--border-radius--050);
  gap: 6px;
  font-weight: bold;
  line-height: 100%;
  /* Rounded Button */
  /* Disabled */
}
.button.border-0 {
  border: 0;
}
.button.button--rounded {
  border-radius: 100px;
  width: 48px;
  justify-content: center;
}
.button:disabled {
  pointer-events: none;
}

/* -------------------------------------------------------------------------
 * BUTTON SIZES
 * ------------------------------------------------------------------------- */
/* Small Button */
.button--sm {
  padding: 11px 12px;
  font-size: var(--wp--preset--font-size--body-sm, 14px);
  /* Rounded Button */
}
.button--sm.button--rounded {
  width: 40px;
}

/* Large Button */
.button--lg {
  padding: 20px;
  font-size: var(--wp--preset--font-size--body-lg, 20px); /* Rounded Button */
}
.button--lg.button--rounded {
  width: 64px;
}

/* -------------------------------------------------------------------------
 * BUTTON VARIANTS
 * ------------------------------------------------------------------------- */
/* Primary Button */
.button--primary, .component-card-product .button,
.product .button {
  background-color: var(--wp--preset--color--primary);
  color: var(--wp--preset--color--white);
  /* Primary Button Reversed */
}
.button--primary:hover, .component-card-product .button:hover,
.product .button:hover {
  background-color: transparent;
  color: var(--wp--preset--color--dark);
  text-decoration: underline;
}
.button--primary:focus, .component-card-product .button:focus,
.product .button:focus {
  box-shadow: 0px 0px 1px 2px #0997ff;
  text-decoration: underline;
}
.button--primary:active, .component-card-product .button:active,
.product .button:active {
  background-color: var(--wp--preset--color--light-030);
  color: var(--wp--preset--color--dark);
  border-color: var(--wp--preset--color--light-030);
}
.button--primary:disabled, .component-card-product .button:disabled,
.product .button:disabled {
  background-color: var(--wp--preset--color--light-020);
  color: var(--wp--preset--color--dark-060);
  border-color: var(--wp--preset--color--light-020);
}
.button--primary.button--reversed, .component-card-product .button--reversed.button,
.product .button--reversed.button {
  background-color: var(--wp--preset--color--white);
  color: var(--wp--preset--color--primary);
  border-color: var(--wp--preset--color--white);
}
.button--primary.button--reversed:hover, .component-card-product .button--reversed.button:hover,
.product .button--reversed.button:hover {
  background-color: var(--wp--preset--color--primary);
  color: var(--wp--preset--color--white);
  text-decoration: underline;
}
.button--primary.button--reversed:active, .component-card-product .button--reversed.button:active,
.product .button--reversed.button:active {
  background-color: var(--wp--preset--color--primary-080);
  color: var(--wp--preset--color--white);
}
.button--primary.button--reversed:disabled, .component-card-product .button--reversed.button:disabled,
.product .button--reversed.button:disabled {
  background-color: var(--wp--preset--color--primary-090);
  color: var(--wp--preset--color--dark-060);
  border-color: var(--wp--preset--color--primary-090);
}

/* Secondary Button */
.button--secondary {
  background-color: var(--wp--preset--color--secondary);
  color: var(--wp--preset--color--black);
  border-color: var(--wp--preset--color--secondary);
  /* Secondary Button Reversed */
}
.button--secondary:hover {
  background-color: var(--wp--preset--color--primary);
  border-color: var(--wp--preset--color--primary);
  color: var(--wp--preset--color--white);
  text-decoration: underline;
}
.button--secondary:focus {
  box-shadow: 0px 0px 1px 2px #0997ff;
  text-decoration: underline;
}
.button--secondary:active {
  background-color: var(--wp--preset--color--primary-090);
  color: var(--wp--preset--color--white);
}
.button--secondary:disabled {
  background-color: transparent;
  color: var(--wp--preset--color--primary-060);
  border-color: var(--wp--preset--color--light-030);
}
.button--secondary.button--reversed {
  background-color: transparent;
  color: var(--wp--preset--color--secondary);
  border-color: var(--wp--preset--color--secondary);
}
.button--secondary.button--reversed:hover {
  background-color: var(--wp--preset--color--secondary);
  color: var(--wp--preset--color--black);
  text-decoration: underline;
}
.button--secondary.button--reversed:active {
  background-color: var(--wp--preset--color--light-020);
  border-color: var(--wp--preset--color--light-020);
  color: var(--wp--preset--color--dark);
}
.button--secondary.button--reversed:disabled {
  background-color: transparent;
  color: var(--wp--preset--color--dark-060);
  border-color: var(--wp--preset--color--primary-060);
}

/* Ghost Button */
.button--ghost {
  background-color: transparent;
  color: var(--wp--preset--color--primary);
  border-color: transparent;
  /* Ghost Button Reversed */
}
.button--ghost:hover {
  background-color: var(--wp--preset--color--light-020);
  color: var(--wp--preset--color--primary);
  text-decoration: underline;
}
.button--ghost:focus {
  box-shadow: 0px 0px 1px 2px #0997ff;
  text-decoration: underline;
}
.button--ghost:active {
  background-color: var(--wp--preset--color--light-030);
  color: var(--wp--preset--color--primary);
}
.button--ghost:disabled {
  background-color: transparent;
  color: var(--wp--preset--color--primary-060);
  border-color: transparent;
}
.button--ghost.button--reversed {
  background-color: transparent;
  color: var(--wp--preset--color--white);
  border-color: transparent;
}
.button--ghost.button--reversed:hover {
  background-color: var(--wp--preset--color--primary-090);
  color: var(--wp--preset--color--white);
}
.button--ghost.button--reversed:active {
  background-color: var(--wp--preset--color--primary-090);
  color: var(--wp--preset--color--white);
}
.button--ghost.button--reversed:disabled {
  background-color: transparent;
  color: var(--wp--preset--color--primary-060);
  border-color: transparent;
}

a:hover {
  text-decoration: underline;
}
a:focus {
  box-shadow: var(--wp--custom--box-shadow--focus);
  text-decoration: underline;
}

p > a[target=_blank]:after {
  display: inline-block;
  content: "\f35d";
  font-family: var(--fa-style-family, "Font Awesome 6 Pro");
  font-weight: inherit;
  margin-left: 4px;
  font-size: 90%;
}

.wicket-breadcrumb .icon {
  display: block;
  margin: 0 10px;
}
.wicket-breadcrumb span.bold {
  font-weight: bold;
}
.wicket-breadcrumb.reversed {
  color: #fff;
}
.wicket-breadcrumb.reversed .icon {
  color: #fff;
}

.social-links ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}
.social-links ul li {
  margin: 0 10px;
}
.social-links ul li:first-child {
  margin-left: 0;
}
.social-links ul li a {
  color: var(--wp--preset--color--black);
  background-color: transparent;
  border: 2px solid transparent;
  white-space: nowrap;
  border-radius: 50%;
  padding: 4px 3px;
  transition: all 0.2s;
}
.social-links ul li a:hover {
  background-color: #d3d4d6;
  border: 2px solid #d3d4d6;
}
.social-links ul li a:focus {
  border-color: var(--wp--preset--color--dark);
}
.social-links--reversed ul li a {
  color: var(--wp--preset--color--white);
}
.social-links--reversed ul li a:hover {
  background-color: #393f46;
  border: 2px solid #393f46;
}
.social-links--reversed ul li a:focus {
  border-color: var(--wp--preset--color--dark);
}

footer .menu-item > a[target=_blank]:after {
  display: inline-block;
  content: "\f35d";
  font-family: var(--fa-style-family, "Font Awesome 6 Pro");
  font-weight: inherit;
  margin-left: 4px;
  font-size: 90%;
}

.component-card-product .onsale,
.product .onsale {
  background-color: var(--wp--preset--color--white);
  padding: var(--wp--preset--spacing--100);
  border-radius: var(--wp--custom--border-radius--100);
  position: absolute;
  top: 12px;
  left: 12px;
  font-weight: bold;
  text-transform: uppercase;
}
.component-card-product .price,
.product .price {
  font-size: var(--wp--preset--font-size--heading-xs);
  display: flex;
  justify-content: center;
  gap: 0.5rem;
}
.component-card-product .price ins,
.product .price ins {
  font-weight: bold;
  text-decoration: none;
}
.component-card-product .button,
.product .button {
  display: inline-flex;
}
.component-card-product .button.product_type_external:after,
.product .button.product_type_external:after {
  display: inline-block;
  content: "\f35d";
  font-family: var(--fa-style-family, "Font Awesome 6 Pro");
  font-weight: inherit;
  margin-left: 4px;
  font-size: 90%;
}
.component-card-product .button .add_to_cart_button__text-added,
.product .button .add_to_cart_button__text-added {
  display: none;
}
.component-card-product .button .add_to_cart_button__text,
.product .button .add_to_cart_button__text {
  display: block;
}
.component-card-product .button.added .add_to_cart_button__text-added,
.product .button.added .add_to_cart_button__text-added {
  display: block;
}
.component-card-product .button.added .add_to_cart_button__text,
.product .button.added .add_to_cart_button__text {
  display: none;
}
.component-card-product .button.button--disabled,
.product .button.button--disabled {
  background-color: var(--wp--preset--color--light-020);
  color: var(--wp--preset--color--dark-060);
  border-color: var(--wp--preset--color--light-020);
  pointer-events: none;
}

.component-card-product__title,
.woocommerce-loop-product__title {
  font-weight: bold;
  text-align: center;
  font-size: var(--wp--preset--font-size--heading-xs);
  line-height: 1.2;
}

/* AlpineJS Helpers */
[x-cloak] {
  display: none !important;
}